          gender: [{required: true, message: ':', trigger: 'blur'}],
          inductionDate: [{required: true, message: ':', trigger: 'blur'}],
          technologyModule: [{required: true, message: ':', trigger: 'blur'}],
          department: [{required: true, message: ':', trigger: 'blur'}],
          inductionPlace: [{required: true, message: ':', trigger: 'blur'}],
          category: [{required: true, message: ':', trigger: 'blur'}],
          customer: [{required: true, message: ':', trigger: 'blur'}],
          positiveTime: [{required: true, message: ':', trigger: 'blur'}],
          graduationTime: [{required: true, message: ':', trigger: 'blur'}],
          degree: [{required: true, message: ':', trigger: 'blur'}],
          phone: [{required: true, message: ':', trigger: 'blur'}],
          salary: [{required: true, message: ':', trigger: 'blur'}],
          probationSalary: [{required: true, message: ':', trigger: 'blur'}]
        },
        talentRules: {
          name: [{required: true, message: ':', trigger: 'blur'}]
        }
      }
        ;
    },
    mounted: function () {
      this.initData();

      this.loadEmps();
    },
    methods: {

      // 
      infoClick(index) {
        this.$alert('table' + this.currentRowIndex, '' + this.menus[index] + '', {
          confirmButtonText: '',
          callback: action => {
            var menu = document.querySelector("#menu");
            menu.style.display = 'none';
          }
        });
      },

      fileUploadSuccess(response, file, fileList) {
        if (response) {
          this.$message({type: response.status, message: response.msg});
        }
        this.loadEmps();
        this.fileUploadBtnText = '';
      },
      mouseEnter(row, column, cell, event) {
        if(this.isShow==false){
          this.isShow=true
        }else{
          this.isShow=false
        }
      },
      uploadResumeSuccess(response, file, fileList) {
        if (response) {
          this.$message({type: response.status, message: response.msg});
        }
        this.loadEmps();
        this.fileUploadBtnText = '';
      },
      fileUploadError(err, file, fileList) {
        this.$message({type: 'error', message: "!"});
        this.fileUploadBtnText = '';
      },
      uploadResumeError(err, file, fileList) {
        this.$message({type: 'error', message: "!"});
        this.fileUploadBtnText = '';
      },
      beforeFileUpload(file) {
        this.fileUploadBtnText = '';
      },
      exportEmps() {
        var ids = ""
        for (var i = 0; i < this.multipleSelection.length; i++) {
          ids += this.multipleSelection[i].id + ",";
        }
        ids = ids.substring(0, ids.lastIndexOf(','));
        window.open("/employee/basic/exportInduction?id=" + ids, "_parent");
      },
      exportExecl(row) {
        window.open("/employee/basic/exportExecl?id=" + row.id, "_parent");
      },
      exportWord(row) {
        window.open("/employee/basic/exportWord?id=" + row.id, "_parent");
      },
      cancelSearch() {
        this.advanceSearchViewVisible = false;
        this.emptyEmpData();
        this.beginDateScope = '';
        this.loadEmps();
      },
      showAdvanceSearchView() {
        this.advanceSearchViewVisible = !this.advanceSearchViewVisible;
        this.keywords = '';
        if (!this.advanceSearchViewVisible) {
          this.emptyEmpData();
          this.beginDateScope = '';
          this.loadEmps();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      updateShowInterview() {
        this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var ids = '';
          for (var i = 0; i < this.multipleSelection.length; i++) {
            ids += this.multipleSelection[i].id + ",";
            //     open(this.multipleSelection[i]);
          }

          this.doUpdateShowInterview(ids);
        }).catch(() => {
        });
      },
      deleteManyEmps() {
        this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var ids = '';
          for (var i = 0; i < this.multipleSelection.length; i++) {
            ids += this.multipleSelection[i].id + ",";
          }
          this.doDelete(ids);
        }).catch(() => {
        });
      },
      deleteEmp(row) {
        this.$confirm('[' + row.name + '], ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
        });
      },
      //
      transferAuthority(row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.tableLoading = true;
          var _this = this;
          this.emp = row;
          debugger
          this.postRequest("/employee/basic/transferAuthority?id=" + row.id).then(resp => {
            _this.tableLoading = false;
            if (resp && resp.status == 200) {
              var data = resp.data;
              _this.loadEmps();
            }
          })
        }).catch(() => {
        });

      },
      doUpdateShowInterview(ids) {
        this.tableLoading = true;
        var _this = this;
        this.putRequest("/talent/basic/updateInterview/", {ids: ids}).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.loadEmps();
          }
        })
      },
      doDelete(ids) {
        this.tableLoading = true;
        var _this = this;
        this.deleteRequest("/employee/basic/emp/" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _
            _this.loadEmps();
          }
        })
      },
      keywordsChange(val) {
        if (val == '') {
          this.loadEmps();
        }
      },
      searchEmp() {
        this.loadEmps();
      },
      currentChange(currentChange) {
        this.currentPage = currentChange;
        this.loadEmps();
      },
      loadEmps() {
        var _this = this;
        this.tableLoading = true;
        this.getRequest("/employee/basic/getInductionByPage?page=" + this.currentPage + "&size=10&name=" + this.keywords + "&hrFlag= YES").then(resp => {
          this.tableLoading = false;
          var _this = this;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.emps = data.emps;
            _this.totalCount = data.count;
//            _this.emptyEmpData();
          }
        })
      },
      addEmp(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.emp.id) {
              //
              this.tableLoading = true;
              this.emp.hrId = 0;
              this.putRequest("/employee/basic/updateInduction", this.emp).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  debugger
                  var data = resp.data;
                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadEmps();
                }
              })
            } else {
              //
              this.tableLoading = true;
              this.emp.hrId = 0;
              this.postRequest("/employee/basic/insertInduction", this.emp).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadEmps();
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      addInduction(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.postRequest("/employee/basic/addInduction", this.induction).then(resp => {
              _this.tableLoading = false;
              if (resp && resp.status == 200) {
                debugger
                var data = resp.data;
                _this.dialogVisible = false;
                _this.emptyEmpData();
                _this.loadEmps();
              }
            })
          } else {
            return false;
          }
        });
      },
      addTalent(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //
            this.tableLoading = true;
            this.postRequest("/employee/basic/updateInterview", this.talent).then(resp => {
              _this.tableLoading = false;
              if (resp && resp.status == 200) {
                var data = resp.data;
                _
                _this.talentDialogVisible = false;
                _this.emptyEmpData();
                _this.loadEmps();
              }
            })
          } else {
            return false;
          }
        });
      },
      cancelEidt() {
        this.dialogVisible = false;
        this.emptyEmpData();
      },
      talentCancelEidt() {
        this.talentDialogVisible = false;
        this.emptyTalentData();
      },
      showDepTree() {
        this.showOrHidePop = !this.showOrHidePop;
      },
      showDepTree2() {
        this.showOrHidePop2 = !this.showOrHidePop2;
      },
      handleNodeClick(data) {
        this.emp.departmentName = data.name;
        this.emp.departmentId = data.id;
        this.showOrHidePop = false;
        this.depTextColor = '#606266';
      },
      handleNodeClick2(data) {
        this.emp.departmentName = data.name;
        this.emp.departmentId = data.id;
        this.showOrHidePop2 = false;
        this.depTextColor = '#606266';
      },
      initData() {
        var _this = this;
        this.getRequest("/system/hr/getCurrentUser").then(resp => {
          if (resp && resp.status == 200) {
            debugger
            this.hr = resp.data;
          }
        })
      },
      showEditEmpView(row) {
        console.log(row)
        this.dialogTitle = "";
        debugger
        this.emp = row;
        this.dialogVisible = true;
      },
      rightClick(row) {
        this.talentDialogTitle = "";
        this.talentDialogVisible = true;
        debugger
        this.talent.name = row.name;
        this.talent.job = row.job;
        this.talent.workAge = row.workAge;
      },
      showEditEmpViewMianshi(row) {
        console.log(row)
        this.dialogTitle = "";
        this.emp = row;
        this.dialogVisible = true;
      },

      emptyEmpData() {
        this.emp = {
          id: null,
          hrId: 0,
          name: null,
          gender: null,
          inductionDate: null,
          technologyModule: null,
          department: null,
          inductionPlace: null,
          graduationTime: null,
          positiveTime: null,
          degree: null,
          workAge: null,
          phone: null,
          salary: null,
          probationSalary: null,
          cstomerQuotation: null,
          grading: null,
          idNumber: null,
          bankCardNumber: null,
          bank: null,
          channel: null,
          company: null,
          note: null,
          item: null,
        }
      },
      emptyTalentData() {
        this.talent = {
          name: "",
          hrId: 0,
          recommendClient: "",
          recommendTime: "",
          job: "",
          workAge: 0,
          progress: ""
        }
      }
    }
  };
</script>
<style>

  #menu {
    width: 120px;
    height: 100px;
    overflow: hidden; /**/
    box-shadow: 0 1px 1px #888, 1px 0 1px #ccc;
    position: absolute;
    display: none;
    background: #ffffff;
    z-index: 10;
  }

  .menu {
    width: 125px;
    height: 25px;
    line-height: 25px;
    text-indent: 10px;
    cursor: pointer;
  }

  .menu:hover {
    color: deeppink;
    text-decoration: underline;
  }


  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }

  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



